sf-space[blackprint]{
	.nodes{
		.node{
			.header .title{
				height: 16px;
				display: flex;
			}

			&.function .header .icon{
				background: url('/assets/img/icon/function.png') no-repeat;
				padding-left: 22px;
			}

			&.func-in-out .header{
				height: 20px;
				.icon{
					margin-top: 2px;
					&:before{
						font-family: "Font Awesome 5 Free";
						font-weight: 400;
						content: "\f277";
						font-size: 16px;
						padding-right: 4px;
						vertical-align: top;
					}
				}
				.text{
					margin-top: 1px;
				}
			}

			&.event .header{
				.icon{
					font-family: "Font Awesome 5 Free";
					font-weight: 900;
					background: none;
					&:before{
						content: "\f542";
					}

					height: 16px;
					font-size: 12px;
					line-height: 18px;
					padding-right: 4px;
					display: inline-block;
				}
			}

			&.input .header{
				.icon{
					font-family: "Font Awesome 5 Free";
					font-weight: 900;
					background: none;
					&:before{
						content: "\f1de";
					}

					height: 16px;
					padding-right: 4px;
					display: inline-block;
				}
			}
		}
	}
}

.node-ui-darker sf-space[blackprint]{
	.nodes{
		.node{
			.header {
				border-bottom: 1px dashed rgba(41,184,229,.7);
				background: rgba(41,184,229,.25);
				box-shadow: none;
			}

			@mixin headerColor($type, $bgA, $bgB, $bgC) {
			  &.#{$type} .header{
				border-bottom: 1px dashed rgba($bgA, $bgB, $bgC, .7);
				background: rgba($bgA, $bgB, $bgC, .25);
			  }
			}

			@include headerColor('event', 226, 18, 18);
			@include headerColor('input', 108, 232, 93);
		}
	}
}
